  { value: 'miscarriage', label: 'Experienced a Miscarriage', emoji: 'ðŸ•Š' },
  { value: 'stillbirth', label: 'Experienced a Stillbirth', emoji: 'ðŸ•¯' },
  { value: 'postpartum', label: 'Postpartum', emoji: 'ðŸ‘¶' }
